import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0, //当前tab索引
			navList: [{
				name: '待付款',
				id: 1
			}, {
				name: '已付款',
				id: 2
			}, {
				name: '已完成',
				id: 3
			}, {
				name: '已取消',
				id: 4
			}, {
				name: '售后',
				id: 5
			}], //tab数组
			headerPosition: "fixed",
			orderList: [], //订单列表
			memberId: uni.getStorageSync(`userInfo`).id, //当前用户id
			pageSize: 10, //条数
			pageNumber: 1, //页数
			status: '', //订单状态
			loadingType: '', //数据加载状态
			url: this.$ctx,
			isCheapPurchase: 0,
		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.pageNumber = 1
		this.orderList = [];
		this.loadData(1);
	},
	//加载更多
	onReachBottom() {
		this.loadData();
	},
	onLoad(options) {
		this.tabCurrentIndex = +options.index || 0;
		this.isCheapPurchase = +options.isCheapPurchase;
		this.getTable();
	},
	methods: {
		goDetail(url){
			uni.navigateTo({
				url
			})
			
		},
		buyBack(item){
			uni.navigateTo({
				url:`/pagesA/user/backSale/backSale?id=${item.order.id}&orderNo=${item.order.sn}`
			})
		},
		// 获取订单状态
		getTable() {
			this.$getJson("/jsp/api/order/getOrderStatus.jsp", {}, "POST", res => {
				// this.navList = res.data;
				this.loadData();
			})
		},
		//获取订单列表
		loadData(e) {
			// this.orderList = [];
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			this.status = navItem.id;
			this.$getJson("/api/v3/my/order/statusData.jsp", {
				pageNumber: this.pageNumber,
				pageSize: this.pageSize,
				status: this.status
			}, "POST", res => {
				let orderList = res.data
				if (orderList.length > 0) {
					if (this.pageNumber == 1) {
						this.orderList = orderList;
					} else {
						this.orderList = this.orderList.concat(orderList);
					}
					this.pageNumber += 1
				} else {
					this.loadingType = 'noMore';
				}
				if (e == 1) {
					uni.stopPullDownRefresh();
				} else {
					uni.hideLoading()
				}
				this.loadingType = orderList.length < 8 ? 'nomore' : 'more';
			});
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			// this.$set(navItem, 'loaded', true);

			//判断是否还有数据， 有改为 more， 没有改为noMore 
			// this.loadingType = 'more';
		},
		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.pageNumber = 1
			this.orderList = [];
			this.loadData();
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
			this.pageNumber = 1
			this.orderList = [];
			// this.loadData()
		},
		//删除订单
		deleteOrder(id, index) {
			uni.showModal({
				content: '是否删除订单？',
				success: (e) => {
					if (e.confirm) {
						let data = {
							data: JSON.stringify({
								id: id,
								memberId: uni.getStorageSync(`userInfo`).id
							})
						}
						this.$getJson("/api/v2/vue/ydd/order/doDelOrder.jsp", data, "POST", res => {
							uni.hideLoading(); //正在加载
							this.orderList.splice(index, 1)
						})
					}
				}
			})
		},
		//取消订单
		cancelOrder(item) {
			uni.showModal({
				content: '是否取消订单？',
				success: (e) => {
					if (e.confirm) {
						this.$getJson("/jsp/api/order/doCancel.jsp", {
							id: item.id,
							memberId: uni.getStorageSync(`userInfo`).id
						}, "POST", res => {
							uni.hideLoading(); //正在加载
							if (res.success == true || res.success == 'true') {
								this.pageNumber = 1
								this.orderList = [];
								this.loadData()
							}
						})
					}
				}
			})

		},
		//复制订单
		copySn(sn) {
			//复制
			uni.setClipboardData({
				data: sn,
				success: function() {
					uni.showToast({
						title: sn + '\r\n复制成功',
						icon: 'none'
					});
				}
			});

		},
		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#ff0000';
			switch (+state) {
				case 1:
					stateTip = '待付款';
					break;
				case 2:
					stateTip = '待发货';
					break;
				case 40:
					stateTip = '订单已关闭';
					stateTipColor = '#999';
					break;
					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		},
		pay(item) {
			uni.navigateTo({
				url: '/pages/money/pay?money=' + item.order.shouldPayAmountShow + '&sn=' + item.order.sn
			});
		}
	},
}
